* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  --blue-color: #00acdc;
}

body {
  margin: 0;
  font-family: Roboto, serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  list-style: none;
  display: flex;
}

.nav__elem:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #000;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: var(--blue-color);
}

.is-active:after {
  content: "";
  background-color: var(--blue-color);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
